.section-title {
    margin-left: 20px;
    font-size: 20px;
    font-weight: bold;
    color: #4a4a4a;
}

.post {
    display: flex;
    border: 1px solid rgba(0, 0, 0, 0.4);
    width: 500px;
    padding: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.6);
    border-radius: 4px;
    margin-bottom: 20px;
    margin-left: 20px;
}

.img-thumb {
    width: 200px;
    height: 150px;
    margin-right: 20px;
}

.img-thumb img {
    width: 200px;
    height: 150px;
    object-fit: cover;
}

.content {
    flex: 1;
}

.content p.title {
    margin: 0;
    font-size: 20px;
    font-weight: bold;
    color: #3d4a6e;
    margin-bottom: 16px;
}

.content p.desc {
    margin: 0;
    font-size: 16px;
    color: #4a4a4a;
}

.remove {
    padding: 12px 18px;
    background-color: red;
    color: white;
    border-radius: 4px;
    outline: none;
    margin-top: 18px;
}

.update {
    padding: 12px 18px;
    background-color: yellowgreen;
    color: white;
    border-radius: 4px;
    outline: none;
    margin-top: 18px;
    margin-right: 5px;
}

.remove:hover, .update:hover {
    cursor: pointer;
}

.form-add-post {    
    box-shadow: 0 0 14px rgba(0, 0, 0, 0.6);
    width: 500px;
    margin-left: 20px;
    padding: 10px;
    margin-bottom: 50px;
    border-radius: 4px;
}

.form-add-post label {
    display: block;
    font-size: 14px;
    color: #607D8B;
    font-weight: bold;
    margin-bottom: 10px;
}

.form-add-post input, .form-add-post textarea {
    outline: none;
    border: 1px solid #607D8B;
    padding: 12px;
    border-radius: 4px;
    margin-bottom: 20px;
    width: 100%;
    box-sizing: border-box;
    font-size: 12;
    color: #607D8B;
}

button.btn-submit {
    outline: none;
    border: 1px solid #2196F3;
    padding: 10px 30px;
    border-radius: 4px;
    color: #2196F3;
    text-transform: uppercase;
    font-weight: bold;
    cursor: pointer;
}